.education{
    background-color: #f4f4f8;
    padding-bottom: 10px;
    margin-left: 13vw;
    width: 87vw;
    height: max-content;
    
 }
 .education>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }

 .education>.skillcont{
  background-color: #fff;
  width: 70vw;
  height: 30vh;
  border-radius: 30px;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  
}
.education>.skillcont>span{
  display: flex;
  font-size: larger;
  font-weight: 500;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 100%;
  background-color: #52cb9f;
  flex: 2;
  border-bottom: 1px solid black;
}
.education>.skillcont>.skillcontent{
  flex:8;
  /* border: 1px solid black; */
}
.skillcontent{
  font-size: 1.4vw;

}
.skillcont span{
  font-weight: 500;
  
}
.skillcontent li{
  margin-left: 20px;
}




  @media only screen and (max-width: 1000px) {
   .education{
     width: 99.5vw;
     margin-left: 0vw;
   }
   .education>.head{
    margin-top: 8vh;
   }
   .education>.skillcont{
    width: 90vw;
   }
   .skillcontent{
    margin-top: 60px;
    font-size: 2.4vw;
    
  }
 }