.desc
{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    /* flex:2; */
    /* background-image: linear-gradient(to right,rgb(234, 120, 194),pink,rgb(226, 29, 226)); */
    background-color:rgb(240, 154, 236);
    position: fixed;
    z-index: 99;
    /* border: 2px solid black; */
    width: 13vw;
    height: 99.5vh;
    display: flex;
    flex-direction: column;
}
.desc>.profile{
    flex: 3;
    border-bottom: 2px solid black;
    
}
.option{
    display: flex;
    flex-direction: column;
    flex:7;

}
.option>.span>.myseet{
   
    display: flex;
    /* justify-content: center; */
    /* text-align: center; */
    color: black;
    text-decoration: none;
    font-size: large;
    /* padding-left: 4px; */
    /* padding: 4px; */
    list-style: none;
    cursor: pointer;
    /* border-bottom: 1px solid rgb(21, 19, 19); */
    transition: background-color .5s ease-out;
}
.option>.span>.myseet>.icon{
    /* margin-left: -7em; */
    
    color: rgb(55, 105, 55);
    margin-right: .5em;
}
.option>.span:hover{
    background-color: #e0b02a;
    border-left: 4px solid black;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.option>.span:focus{
    background-color: #e0b02a;
    border-left: 4px solid black;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.profile>img{
    margin: auto;
   border: 2px solid black;
    max-width: 100%;
    height: auto;
    border-radius: 50%;
    margin: 30px;
}



@media only screen and (max-width: 1000px) {
    .desc{
      display:none;
    }
  }
  