
.home>.detail>.name{
    color: rgb(8, 145, 56);
    font-size:60px;
        font-weight: 400;
}
.home>.detail>.role{

    display: flex;
    font-size: xx-large;
        font-weight: 400;
        color: rgb(16, 62, 62);
}
.home>.detail>p{
    font-size: 30px;
    color: rgb(115, 69, 141);
}
.home{
    /* flex:8; */
    /* background-color: #646899; */
    display: flex; 
    justify-content: center; 
    color: #323336;
    align-items: center;
    background-image: linear-gradient(to right,rgb(234, 120, 194),pink,rgb(226, 147, 29));
    margin-left: 13vw;
    width: 87vw;
    height: 99.5vh;
    /* padding-right: 20px; */
    /* border: 2px solid black; */
    
}
.roletype{
    margin-left: .2em;
}
.role>div{
    margin-left: 1rem;
    color: rgb(145, 66, 54);
    text-decoration: underline;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .10em solid pink; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    animation: 
        typing 2s steps(40, end) infinite,
        blink-caret .90s step-end infinite;
}
@keyframes typing {
    from { width: 0 }
    to { width: 20% }
}
   @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color:rgb(145, 66, 54); }
  }  

 .detail>.icon .ic{
    font-size: 40px;
    margin-right: .2em;
    margin-top: .2em;
    padding: 10px;
    color: #551a8b;
 }
 .detail>.icon .ic:hover{
    background-color: #171b3b;
    border-radius: 50%;
    color: rgb(115, 222, 115);
 }


 @media only screen and (max-width: 1000px) {
    .home{
        margin-top: 8vh;
      width: 99.5vw;
      margin-left: 0vw;
      display: block;
      
    }
    .home>.detail{
        margin-left: 10px;
        align-self: center;
        /* display: block; */
        padding-top: 60px;
        /* margin-top: -26vh; */
    }
    
    .home>.detail>.name{
        font-size:40px;
            font-weight: 400;
    }
    .home>.detail>.role{
        margin-top: 10px;
        display: flex;
        font-size: 30px;
            font-weight: 700;
    }
  }