.skills{
    background-color: #f4f4f8;
    
    margin-left: 13vw;
    width: 87vw;
    padding-bottom: 10px;
    height:max-content ;
    
 }
 .skills>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .skills>.skillset{
    background-color: #fff;
    width: 70vw;
    height: 30vh;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    margin: 20px auto;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    
 }
 .skills>.skillset>span{
    display: flex;
    font-size: larger;
    font-weight: 500;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 90%;
    flex: .2;
    border-bottom: 1px solid black;
 }
 .skillcontents{
   padding-left: 10px;
   padding-right: 10px;
   display: flex;
   overflow-x: scroll;
   align-items: center;
 }
 .skillnames{
   display: flex;
   margin-right: 4vw;
   flex-direction: column;
 }
 .skillnames>img{
   max-width: 80px;
   height: 80px;

 }
 .skillnames>span{
   text-align: center;

 }
 .skills>.skillset>.skillcontents{
   
    flex:.8;
    /* border: 1px solid black; */
 }

 @media only screen and (max-width: 1000px) {
   .skills{
     width: 99.5vw;
     margin-left: 0vw;
   }
   .skills>.head{
      margin-top: 8vh;
   }
   .skillcontents{
      margin-right: 20px;
   }
 }