.resume{
    background-color: #f4f4f8;
    /*  */
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 13vw;
    width: 87vw;
    /* padding-bottom: 10px; */
    height:max-content;
    
 }
 .resume>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .resume>object{
   width: 100%;
   height: 90vh;
 }

 @media only screen and (max-width: 1000px) {
   .resume{
      margin-top: 8vh;
     width: 100%;
     margin-left: 0vw;
   }
   
 }
