.about{
    background-color: #f4f4f8;
   padding-bottom: 10px;
    margin-left: 13vw;
    width: 87vw;
    height: max-content;
    
 }
 .about>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 .about>p{
    padding: 15px;
    font-size: x-large;
 }
 .about>p>p>span{
    font-weight: 900;
 }

 @media only screen and (max-width: 1000px) {
   .about{
     width: 99vw;
     margin-left: 0vw;
   }
   .about>.head{
      margin-top: 8vh;
   }
 }


 