.project{
    background-color: #f4f4f8;
    
    margin-left: 13vw;
    width: 87vw;
    height: max-content;
    padding-bottom: 10px;
    
 }
.projcontainer{
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
}
 .projcontainer>.skillcont{
   background-color: #fff;
   /* display: inline-block; */
   width: 24vw;
   height: 40vh;
   /* border: 1px solid black; */
   display: flex;
   flex-direction: column;
   margin: 20px;
   box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
   
 }
 .projcontainer>.skillcont>img{
   flex:0.6;
   height: 50%;
   border: 1px solid black;
 }

 .projcontainer>.skillcont>.projcont{
   flex:0.4;
   border: 1px solid black;
   /* border: 1px solid black; */
 }


 .project>.head{
    width: 100%;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    height:10vh ;
    color: #fff;
    background-image: linear-gradient(to right,rgb(184, 27, 129),rgb(184, 37, 61),rgb(233, 174, 86));
    background-color: #ec7abb;
 }
 


 @media only screen and (max-width: 1000px) {
   .project{
     width: 99.5vw;
     margin-left: 0vw;
   }
   .project>.head{
      margin-top: 8vh;
   }
   .projcontainer{
      display: grid;
      grid-template-columns: 1fr ;
   }
   .projcontainer>.skillcont{
      width: 70vw;
      margin: 20px auto;
   }
   
 }