.scrside{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    /* flex:2; */
    /* background-image: linear-gradient(to right,rgb(234, 120, 194),pink,rgb(226, 29, 226)); */
    /* background-color:rgb(242, 191, 235); */
    background-color: rgb(240, 154, 236);;
    position: fixed;
    z-index: 9900;

    /* border: 2px solid black; */
    width: 50vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    right: -50vw;
    transition: transform .5s ease-in;
    /* animation: 2s slidee; */
}
.desc>.profile{
    flex: 3;
    border-bottom: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}
.option{
    display: flex;
    flex-direction: column;
    flex:7;

}
.option>.span{
    display: flex;
    /* justify-content: center; */
    /* text-align: center; */
    color: black;
    text-decoration: none;
    font-size: larger;
    padding: 10px;
    list-style: none;
    cursor: pointer;
    border-bottom: 1px solid rgb(21, 19, 19);
    transition: background-color .5s ease-out;
}
.option>.span>.icon{
    margin-left: 1em;
    color: rgb(55, 105, 55);
    margin-right: .5em;
}
.option>.span:hover{
    background-color: #e0b02a;
    border-left: 4px solid black;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.option>.span:focus{
    background-color: #e0b02a;
    border-left: 4px solid black;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.profile>img{
   border: 2px solid black;
    width: 10em;
    border-radius: 30px;
}
.closescr{
    z-index: 9902;
    align-self: flex-end;
    width: 2em;
    height: 2em;
    cursor: pointer;
}

.animate{
    /* animation: .5s slidee; */
    transform: translateX(-50vw);
}
.animatecl{
    /* animation: .5s slidee; */
    transform: translateX(50vw);
    /* right: -13vw; */
}

